















































export default {
	data() {
		return {
			categoryList: [],  //一级分类
			subCategoryList: [],  //二级商品分类
			height: 0,  //页面高度
			categoryActive: 0, 
			scrollTop: 0,
			scrollHeight: 0, 
		}
	},
	methods: {
		// 右边商品的滑动高度
		scroll(e) {
			this.scrollHeight = e.detail.scrollHeight;
		},
		// 点击分类切换
		categoryClickMain(categroy, index) {
			this.categoryActive = index; //当前点击类别的下标
			this.typeId = categroy.ty_id;  //当前点击类别的类别id
			uni.request({
				url:'http://192.168.199.194/mobile/index/getType',
				method:'GET',
				data:{
					tyid: this.typeId
				},
				success: (res) => {
					if(res.statusCode == 200){
						this.subCategoryList = res.data.setype; //重新给subCategoryList二级分类赋值【会自动替换内容】
					}
				}
			})
		},
		getCategory() {},
		// 顶部返回箭头
		backTap(e){
			uni.switchTab({
				url: '../index/index'
			})
		}
	},
	// 初始化页面时
	onLoad: function () {
		this.getCategory();
		this.height = uni.getSystemInfoSync().windowHeight; //页面高度随window高度自动增高
		uni.request({
			url:'http://192.168.199.194/mobile/index/getType',
			method: 'GET',
			data:{},
			success: res => {
				if(res.statusCode == 200){
					this.categoryList = res.data.goodsType; //给categoryList一级分类赋值
					this.subCategoryList = res.data.setype; //给subCategoryList二级分类赋值
				}
			},
			fail: () => {},
			complete: () => {}
		});
	},
	
}
